import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { currentMomentDate, format, formatRef, InputGroupInline, InputType, REF_PATTERN, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../dependencies/empresaMLFDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, PrevisaoFaturamentoBrutoRV, REPORT_EVENT, REPORT_TITLE } from './previsaoFaturamentoBrutoRV';

const current = currentMomentDate();

const DEFAULT_VALUES: PrevisaoFaturamentoBrutoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  anoReferencia: format(current.toDate(), 'YYYY'),
  gerarCabecalhoPadraoAdministradora: true,
  inicioPrevisao: formatRef(current.toDate(), REF_PATTERN),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
};

const defaultWidth: number = 135;

const PrevisaoFaturamentoBrutoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const { handleSubmit, ...formProps } = useForm<PrevisaoFaturamentoBrutoRV>({ mode: 'onBlur' });

  const validateExtras = (formData: PrevisaoFaturamentoBrutoRV): string[] => {
    const errors = [];
    if (formData.anoReferencia && formData.inicioPrevisao && formData.anoReferencia > formData.inicioPrevisao) {
      errors.push('O campo Início da Previsão não pode ser anterior ao campo Ano Referência.');
    }
    return errors;
  };

  return (
    <WindowComponent
      title="Previsão de Faturamento Bruto"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
      wikiId={3074}
    >
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, maxLength: 4 } }}
            id="anoReferencia"
            isPrependLabel={true}
            label="Ano Referência"
            labelWidth={defaultWidth}
          />
        </Col>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="inicioPrevisao"
            isPrependLabel={true}
            label="Início da Previsão"
            type={InputType.REFERENCE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { max: 12, min: 1, required: true } }}
            id="nMesesParaPrevisao"
            isPrependLabel={true}
            label="Nº de Meses"
            labelWidth={defaultWidth}
            placeholder="Informe o Número de Meses"
            type={InputType.NUMBER}
          />
        </Col>
      </Row>

      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      />
    </WindowComponent>
  );
};

export default PrevisaoFaturamentoBrutoReportView;
